import { Component, OnInit } from '@angular/core';
import { DatabaseService } from 'src/app/services/database.service';
import { ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-studentdetails',
  templateUrl: './studentdetails.component.html',
  styleUrls: ['./studentdetails.component.scss']
})
export class StudentdetailsComponent implements OnInit {

   //declaring variables
   Ref:any;
   student : any;
 
  constructor(public _Data: DatabaseService, public _route : ActivatedRoute) { }

  //update student
  UpdateUser(userInfo: NgForm){
    this._Data.UpdatePerson(userInfo.value,this.Ref).then(ref =>{
      alert("successfully updated")
    })

  }

  //delete student
  deleteUser(ref){
    this.Ref=this._route.snapshot.paramMap.get('ref');
    this._Data.deletePerson(this.Ref);
  }


  ngOnInit() {

   //get reference
    this.Ref = this._route.snapshot.paramMap.get('ref');
    console.log( 'Ref:',this.Ref);

      this._Data.getStudentInfo(this.Ref).subscribe(action =>{
       console.log(action);
       this.student=action;
     })
     
  }

}

